.HoverDropdown {
  /* The container <div> - needed to position the dropdown content */
  position: relative;
  display: inline-block;
  /* Links inside the dropdown */
}
.HoverDropdown .dropbtn {
  padding: 16px;
}
.HoverDropdown .dropbtn .btn {
  width: 150px;
  height: 50px;
  border: 1px solid #333;
  font-family: "Nanum Gothic", sans-serif;
  font-size: 15px;
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  position: relative;
  z-index: 0;
  transition: 1s;
}
.HoverDropdown .dropbtn .btn::before {
  position: absolute;
  background: #fff;
  z-index: -1;
  transition: 1s;
  content: "";
  width: 130px;
  height: 50px;
}
.HoverDropdown .dropbtn .btn::after {
  position: absolute;
  background: #fff;
  z-index: -1;
  transition: 1s;
  content: "";
  width: 150px;
  height: 30px;
}
.HoverDropdown .dropbtn .btn:hover {
  background: #fff;
}
.HoverDropdown .dropbtn .btn:hover .noselect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.HoverDropdown .dropdown-content {
  /* Dropdown Content (Hidden by Default) */
  display: none;
  position: absolute;
  min-width: 160px;
  z-index: 1;
  margin-left: 11px;
}
.HoverDropdown .dropdown-content a {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  background-color: white;
  border: #ddd solid 1px;
}
.HoverDropdown .dropdown-content a:hover {
  /* Change color of dropdown links on hover */
  background-color: #ddd;
}
.HoverDropdown:hover .dropdown-content {
  /* Show the dropdown menu on hover */
  display: block;
}
.HoverDropdown:hover .dropbtn .btn::before {
  width: 0px;
  background: #fff;
}
.HoverDropdown:hover .dropbtn .btn::after {
  height: 0px;
  background: #fff;
}

